.App {
  text-align: center;
  max-width: 500px;
  margin: 0 auto;
}

h1 {
  text-transform: uppercase;
  border-bottom: 1px solid black;
  letter-spacing: 0.2rem;
  font-weight: 700;
  font-size: 36px;
}

footer {
  font-size: 0.8em;
  margin: 30px 0;
}

#gameboard {
  margin: 30px 0;
}

#keyboard button {
  border-radius: 4px;
  font-weight: bold;
  cursor: pointer;
  user-select: none;
  background-color: #d3d6da;
  color: #1a1a1b;
  border: 0;
  margin: 0 3px;
  height: 58px;
  flex: 1;
}

.game-row {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 6px;
}

.tile {
  border: 2px solid #d3d6da;
  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none;
  width: 50px;
  height: 50px;
  margin: 0 3px;
  font-size: 2rem;
  font-weight: bold;
  box-sizing: border-box;
}

.bull {
  background-color: #6aaa64;
  border: 0;
  color: #fff;
}

.cow {
  background-color: #c9b458;
  border: 0;
  color: #fff;
}

.none {
  background-color: #787c7e;
  border: 0;
  color: #fff;
}

#keyboard {
  display: flex;
  flex-direction: row;
}

#overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
}

#eog-dialog {
  background-color: #fff;
  margin: 0 auto;
  width: 90%;
  max-width: 400px;
  border-radius: 10px;
  padding: 20px;
}

#eog-dialog button {
  border-radius: 4px;
  font-weight: bold;
  cursor: pointer;
  user-select: none;
  background-color: #d3d6da;
  color: #1a1a1b;
  border: 0;
  margin: 0 3px;
  padding: 10px;
  flex: 1;
}
